@import '../../../asset/style/variable';

.btn {
  width: 100%;
  height: 42px;
  border: none;
  border-radius: $border-radius;
  font-size: $font-size-lg;
  font-weight: $font-weight-xl;
  text-align: center;
  vertical-align: middle;
  color: $snow;
  background-color: $blue;
  opacity: $opacity-blur;
  &:hover {
    opacity: $opacity-default;
  }
  &:disabled {
    color: $stone;
    background-color: $raven;
    opacity: $opacity-default;
  }
}
